

const Accordion = ({children, title}) => {
    const accordionOpen = false;
    return (
        <div className={'cl-accordion'+ ' '+accordionOpen?'open':''} onClick = {()=>accordionOpen = !accordionOpen }>
            <div className="accordion-head">
                <h1>{title}</h1>
            </div>
            {children}
        </div>
    )
}

export default Accordion;

